import React from "react";
import { ListGroup } from "react-bootstrap";

export default function (props: { keywords: string; location: string }) {
  const isLocationHelsinki =
    props.location.toLocaleLowerCase().trim() == "helsinki";
    
  return (
    <ListGroup className="mb-5">
      <ListGroup.Item>
        <a
          target="_blank"
          href={`https://www.linkedin.com/jobs/search/?geoId=90010124&keywords=${props.keywords}&location=${props.location}&sortBy=DD`}
        >
          LinkedIn
        </a>
      </ListGroup.Item>
      {isLocationHelsinki ? (
        <ListGroup.Item>
          <a
            target="_blank"
            href={`http://www.jobsinhelsinki.com/search?q=${props.keywords}`}
          >
            Jobs in Helsinki
          </a>
        </ListGroup.Item>
      ) : null}
      <ListGroup.Item>
        <a
          target="_blank"
          href={`https://fi.indeed.com/jobs?q=${props.keywords}&l=${props.location}&ts=1586948751141&pts=1586431031601&rq=1&rsIdx=0&fromage=last&newcount=1`}
        >
          Indeed
        </a>
      </ListGroup.Item>
      <ListGroup.Item>
        <a
          target="_blank"
          href={`https://duunitori.fi/tyopaikat/?haku=${props.keywords}&alue=${props.location}`}
        >
          Duunitori
        </a>
      </ListGroup.Item>
      <ListGroup.Item>
        <a
          target="_blank"
          href={`https://thehub.io/jobs?search=${props.keywords}&location=${props.location},%20Finland&countryCode=FI`}
        >
          The Hub
        </a>
      </ListGroup.Item>
      <ListGroup.Item>
        <a
          target="_blank"
          href={`https://www.careerjet.fi/wetsi/tyot?s=${props.keywords}&l=${props.location}&lid=254611&nw=1`}
        >
          Careerjet
        </a>
      </ListGroup.Item>
      <ListGroup.Item>
        <a
          target="_blank"
          href={`https://fi.neuvoo.com/jobs/?k=${props.keywords}&l=${props.location}&p=1&date=7d&field=&company=&source_type=&radius=&from=&test=&iam=&is_category=no`}
        >
          Neuvoo
        </a>
      </ListGroup.Item>
      <ListGroup.Item>
        <a
          target="_blank"
          href={`https://www.monster.fi/tyopaikat/haku/?q=${props.keywords}&where=${props.location}`}
        >
          Monster.fi
        </a>
      </ListGroup.Item>
      {isLocationHelsinki ? (
        <React.Fragment>
          <ListGroup.Item>
            <a
              target="_blank"
              href={`https://www.glassdoor.com/Job/jobs.htm?suggestCount=0&suggestChosen=false&clickSource=searchBtn&typedKeyword=${props.keywords}&sc.keyword=${props.keywords}&locT=C&locId=2440450&jobType=`}
            >
              Glassdoor
            </a>
          </ListGroup.Item>
          <ListGroup.Item>
            <a
              target="_blank"
              href={`https://tyopaikat.oikotie.fi/haku?sijainti[1]=1&jq=${props.keywords}&sort_by=publication_time&page=0&ref=otty_etusivu_pikahaku`}
            >
              Oikotie
            </a>
          </ListGroup.Item>
          <ListGroup.Item>
            <a
              target="_blank"
              href={`https://graduateland.com/jobs/jobs?keyword=${props.keywords}&cities%5B0%5D=11919780`}
            >
              Graduateland
            </a>
          </ListGroup.Item>
        </React.Fragment>
      ) : null}
      <ListGroup.Item>
        <a
          target="_blank"
          href={`https://fi.jooble.org/ty%C3%B6paikat-${props.keywords}/${props.location}`}
        >
          Jooble
        </a>
      </ListGroup.Item>
      <ListGroup.Item>
        <a
          target="_blank"
          href={`https://eu.experteer.com/jobs-finland-${props.location}_-${props.keywords}_lid55cid2300001?sort=most_recent`}
        >
          Experteer
        </a>
      </ListGroup.Item>
      <ListGroup.Item>
        <a
          target="_blank"
          href={`https://avointyopaikka.fi/avoimet-${props.keywords}-tyopaikat-${props.location}#search`}
        >
          Avointyopaikka
        </a>
      </ListGroup.Item>
    </ListGroup>
  );
}
